@import "~styles/variables";  // '~' informs sass-loader that is not relative path

#header {
	background: $almost-black;
	height: 50px;
	width: 100%; }

.header__logo {
	padding-top: 5px;
	height: 35px;
	margin: 0 auto;
	text-align: center;
	display: block; }


.header__btn-back {
	color: $white;
	float: left;
	cursor: pointer;
	text-transform: uppercase;

	&::before {
		content: '<-';
		display: block;
		float: left;
		padding-right: 5px;
		margin-left: 10px; } }
